import React, { FC, useEffect, useRef, useState } from "react";
import { useClassname } from "../../libs/css";

import "./styles.scss";



const Video: FC<{href: string, highlight?: string}> = ({href, highlight = ''}) => {
    const className = useClassname("video");

    const [prefix, other] = highlight.split('<em>');
    const [content, postfix] = (other || '').split('</em>');
    const [isVisible, setVisible] = useState(false);

    const ref = useRef<HTMLDivElement>(null);

    useEffect(() => {
        const intersectionCallback = (entries: IntersectionObserverEntry[]) => {
            entries.forEach((entry) => {
                if (entry.intersectionRatio > 0) {
                    setVisible(true);
                } else {
                    setVisible(false);
                }
                
            });
        };

        const observer = new IntersectionObserver(intersectionCallback, {
            threshold: 0,
        });

        ref.current && observer.observe(ref.current);
        return () => {
            observer.unobserve
        }
    }, []);

    return (
        <div className={className('wrapper')} ref={ref}>
            {isVisible && (<video controls className={className()} preload="">
                <source src={href} type="video/webm" />
            </video>)}
            {highlight && (
                <div className={className('highlight')}>
                    {prefix}<span className={className('search')}>{content}</span>{postfix}
                </div>
            )}
        </div>
    );
};

export default Video;
