/* eslint-disable @typescript-eslint/no-empty-function */
import React, { FC, PropsWithChildren, useCallback, useEffect, useState } from "react";
import { useClassname } from "../../libs/css";
import bg from './bg.png';
import close from './close.svg';
import attach from './attach.svg';
import high from './high.svg';
import slow from './slow.svg';
import speaker from './speaker.svg';
import film from './film.svg';
import ok from './ok.svg';
import loader from './loader.svg';
import alarm from './alarm.svg';
import "./styles.scss";
import { ConfigProvider, Flex, Input, Slider } from "antd";
import { Button } from "../button";
import Tag from "../tag";
import dayjs, { Dayjs } from "dayjs";
import relativeTime from "dayjs/plugin/relativeTime";
import Video from "../video";
import useApiClient from "../../libs/api-client";
dayjs.extend(relativeTime);

const className = useClassname("upload-modal");

export const AccentText: FC<PropsWithChildren> = ({children}) => {
    return (
        <span className={className('accent-text')}>
            {children}
        </span>
    );
};

const formatSeconds = (seconds: number) => {
    const mins = Math.round(seconds / 60);
    const secs = seconds - mins * 60;
    return `${mins < 10 ? '0' : ''}${mins}:${secs < 10 ? '0' : ''}${secs}`
}

const getUploadInfo = (value: number) => {
    const fast = {
        title: <><img src={high} /> Быстрый</>,
        subtitle: 'Видео обработается с максимальной скоростью',
        align: 'flex-start',
    };

    return {
        1: fast,
        2: {
            title: <><img src={ok} /> Оптимальный</>,
            subtitle: 'Извлекает признаки точнее',
            align: 'center',
        },
        3: {
            title: <><img src={slow} /> Детальный</>,
            subtitle: 'Извлекает признаки с максимальной точностью, но требует больше времени',
            align: 'flex-end',
        }
    }[value] || fast;
}

enum UploadError {
    Empty,
    Api,
}

export const UploadModal: FC<{isVisible: boolean, onClose: () => void}> = ({isVisible, onClose}) => {
    const apiClient = useApiClient();

    const [uploadType, setUploadType] = useState(1);
    const uploadInfo = getUploadInfo(uploadType);

    const [fileLink, setFileLink] = useState('');

    const [uploadError, setUploadError] = useState<UploadError>();

    const [isUploading, setIsUploading] = useState<Dayjs>();
    const [, setTimer] = useState(0);

    const [uploadResult, setUploadResult] = useState<{
        href?: string,
        videoText?: string,
        videoTime?: number,
        audioText?: string,
        audioTime?: number,
    }>();

    useEffect(() => {
        const i = setInterval(() => {
            setTimer(prev => prev + 1);
        }, 1000);

        return () => {
            clearInterval(i);
        }
    }, [setTimer]);

    const handleSubmit = useCallback(() => {
        if (!fileLink) {
            setUploadError(UploadError.Empty);
            return;
        }

        setUploadError(undefined);
        setIsUploading(dayjs());

        apiClient.upload({url: fileLink, quality: uploadType}).then(({error, url, audioText, audioTime, videoText, videoTime}) => {
            setIsUploading(undefined);
            if (error) {
                setUploadError(UploadError.Api);
            } else {
                setUploadResult({
                    href: url,
                    audioText,
                    audioTime,
                    videoText,
                    videoTime,
                });
            }
        })
    }, [fileLink, uploadType]);

    const reset = useCallback(() => {
        setIsUploading(undefined);
        setUploadResult(undefined);
        setUploadError(undefined);
        setFileLink('');
        setUploadType(1);
    }, []);

    const handleClose = useCallback(() => {
        reset();

        onClose();
    }, []);

    if (!isVisible) {
        return null;
    }

    return (
        <div className={className('wrapper')}>
            <Flex vertical justify="space-between" className={className()} style={{background: `url(${bg})`}}>
                <div className={className('close')} onClick={handleClose}><img src={close} /></div>
                <div className={className('form')}>
                    <div className={className('title')}>
                        {uploadResult && (
                            <>
                                Видео успешно <AccentText>загружено!</AccentText>
                            </>
                        )}
                        {isUploading && (
                            <>
                                Ожидайте видео <AccentText>загружается</AccentText>
                            </>
                        )}
                        {!isUploading && !uploadResult && (
                             <>
                             Загрузите <AccentText>видео</AccentText> и определите <AccentText>метод</AccentText> обработки
                         </>
                        )}
                    </div>
                    {uploadResult && (
                        <div className={className('subtitle')}>Вы можете проверить качество и оценить скорость загрузки видео</div>
                    )}
                    {isUploading && (
                        <>
                            <Flex align="center" vertical className={className('loader')}><img src={loader} /></Flex>
                            <Flex align="center" vertical>
                                <Tag>
                                    <img src={alarm} />
                                    {formatSeconds(dayjs().diff(isUploading, 's'))}
                                </Tag>
                            </Flex>
                        </>
                    )}
                    {uploadResult && (
                        <div className={className('result')}>
                            <Flex gap={16}>
                                <div className={className('result-video')}>
                                    <Video href={uploadResult.href || ''} />
                                </div>
                                <Flex vertical gap={24}>
                                    <Flex gap={8} wrap="wrap">
                                        <Tag>{uploadInfo.title}</Tag>
                                        {uploadResult.audioTime && (<Tag><img src={speaker} />{formatSeconds(Math.ceil(uploadResult.audioTime))}</Tag>)}
                                        {uploadResult.videoTime && (<Tag><img src={film} />{formatSeconds(Math.ceil(uploadResult.videoTime))}</Tag>)}
                                    </Flex>

                                    {uploadResult.videoText && (
                                        <div>
                                            <div className={className('result-title')}>
                                                Описание видеодорожки
                                            </div>
                                            <div className={className('result-subtitle')}>
                                                {uploadResult.videoText}
                                            </div>
                                        </div>
                                    )}

                                    {uploadResult.audioText && (
                                        <div>
                                            <div className={className('result-title')}>
                                                Описание аудиодорожки
                                            </div>
                                            <div className={className('result-subtitle')}>
                                                {uploadResult.audioText}
                                            </div>
                                        </div>
                                    )}
                                </Flex>
                            </Flex>
                        </div>
                    )}
                    {!isUploading && !uploadResult && (
                        <>
                            <ConfigProvider
                                theme={{
                                    components: {
                                        Input: {
                                            colorBgContainer: '#25252C',
                                            colorBorder: 'transparent',
                                            hoverBorderColor: 'transparent',
                                            activeBg: '#25252C',
                                            hoverBg: '#25252C',
                                            inputFontSize: 18,
                                            paddingBlock: 12,
                                            paddingInline: 0,
                                            colorText: 'white',
                                            colorTextPlaceholder: '#9693A1',
                                        },
                                    },
                                }}
                            >
                                <div className={className({elem: 'input', error: uploadError === UploadError.Empty})}>
                                    <Input
                                        placeholder="Добавьте ссылку на видео"
                                        variant="borderless"
                                        value={fileLink}
                                        onChange={e => {
                                            setUploadError(undefined);
                                            setFileLink(e.currentTarget.value);
                                        }}
                                        onFocus={() => setUploadError(undefined)}
                                        prefix={<img src={attach} />}
                                        autoComplete="off"
                                        autoCorrect="off"
                                        autoCapitalize="off"
                                        spellCheck="false"
                                    />
                                </div>
                            </ConfigProvider>
                            <ConfigProvider
                                theme={{
                                    components: {
                                        Slider: {
                                            railBg: '#00E2B8',
                                            railHoverBg: '#00E2B8',
                                            railSize: 4,
                                            trackBg: '#00E2B8',
                                            trackHoverBg: '#00E2B8',

                                            handleColor: '#00E5BD',
                                            handleActiveColor: '#00E5BD',
                                            colorBgElevated: '#182F2F',
                                        },
                                    },
                                }}
                            >
                                <div className={className('slider')}>
                                    <Slider min={1} max={3} value={uploadType} onChange={setUploadType} tooltip={{ open: false }} />
                                </div>
                                <div className={className('slider-tags')}>
                                    <Flex vertical gap={4} align={uploadInfo.align}>
                                        <Tag>{uploadInfo.title}</Tag>
                                        <Tag>{uploadInfo.subtitle}</Tag>
                                    </Flex>
                                </div>
                                {uploadError && <div className={className('error')}>Произошла ошибка загрузки</div>}
                            </ConfigProvider>
                        </>
                    )}
                </div>
                {!isUploading && !uploadResult && <Button onClick={handleSubmit}>Загрузить видео</Button>}
                {uploadResult && <Button onClick={reset}>Загрузить еще</Button>}
            </Flex>
            <div className={className('fade')} onClick={handleClose} />
        </div>
    );
};
