import { useCallback, useState } from "react";
import { useLocalStorage } from "./local-storage";
// import useApiClient from "./api-client";

export enum SearchSuggestType {
    Time,
    Popular
}

export interface SearchSuggest {
    type: SearchSuggestType;
    value: string;
    delete?: () => void;
}

const parseSearchHistory = (data: string): string[] => {
    try {
        return JSON.parse(data);
    } catch (e) {
        console.log(e);
        return [];
    }
};

export const useSuggests = (search: string): [SearchSuggest[], (value: string) => void] => {
    const ls = useLocalStorage();
    const searchHistory = ls.getLs('sqwozcode-search-history');
    const timeItems = parseSearchHistory(searchHistory || "[]");

    // const apiClient = useApiClient();

    const [savedTimeItems, setSavedTimeItems] = useState(timeItems);
    // const [savedApiItems, setSavedApiItems] = useState<SearchSuggest[]>([]);

    // useEffect(() => {
    //     apiClient.getSuggests({search}).then(result => {
    //         setSavedApiItems(result.map(r => ({
    //             type: SearchSuggestType.Popular,
    //             value: r,
    //         })));
    //     })
    // }, [search])

    const removeSuggest = useCallback((value: string) => {
        setSavedTimeItems(prev => {
            const newItems = [...prev.filter(v => v.toLowerCase() !== value.toLowerCase())];
            ls.setLs('sqwozcode-search-history', JSON.stringify(newItems));
            return newItems;
        });
        
    }, []);

    const items = [
        ...savedTimeItems.map(item => ({
            type: SearchSuggestType.Time,
            value: item,
            delete: () => {
                removeSuggest(item)
            }
        })),
    ].filter(({value}) => value.toLowerCase().includes(search.toLowerCase()));

    const saveSuggest = useCallback((value: string) => {
        setSavedTimeItems(prev => {
            const newItems = [...prev.filter(v => v.toLowerCase() !== value.toLowerCase()), value].filter((_v, i) => i < 10);
            ls.setLs('sqwozcode-search-history', JSON.stringify(newItems));
            return newItems;
        });
        
    }, []);

    // return [[...items, ...savedApiItems], saveSuggest];
    return [items, saveSuggest];
};
