import React, { FC, useCallback, useEffect, useRef, useState } from "react";
import { useClassname } from "../../libs/css";

import "./styles.scss";
import { ConfigProvider, Flex, Input, InputRef } from "antd";

import search from './search.svg';
import whiteSearch from './white-search.svg';
import time from './time.svg';
import close from './close.svg';
import { Button } from "../button";
import { SearchSuggestType, useSuggests } from "../../libs/suggests";
import { Link } from "react-router-dom";
import { Routes } from "../../libs/application-routes";

const className = useClassname("search-input");

export const SearchInput: FC<{
    value: string;
    onChange: (value: string) => void;
    onSearch: () => void;
    useSearchButton?: boolean;
}> = ({value, onChange, onSearch, useSearchButton}) => {
    const [suggests, saveSuggest] = useSuggests(value);
    const [isFocused, setFocused] = useState(false);
    const [isSuggestFocused, setSuggestFocused] = useState(false);

    const inputRef = useRef<InputRef>(null);
    const suggestRef = useRef<HTMLDivElement>(null);

    const handleChange = useCallback((value: string) => {
        onChange(value);
    }, [])

    useEffect(() => {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const cb = (e: any) => {
            const target = e.target;
            
            if (suggestRef.current !== target && !suggestRef.current?.contains(target) && !isFocused) {
                setSuggestFocused(false);
            }
        }

        document.addEventListener('click', cb);

        return () => {
            document.removeEventListener('click', cb);
        }
    }, [isFocused]);

    useEffect(() => {
        if (isFocused) {
            setSuggestFocused(true);
        }
    }, [isFocused]);

    const handleSearch = useCallback(() => {
        if ((/^\s*$/.test(value))) {
            return;
        }

        saveSuggest(value);
        onSearch();
        inputRef.current?.blur();
        setSuggestFocused(false);
    }, [value]);

    return (
        <ConfigProvider
            theme={{
                components: {
                    Input: {
                        colorBgContainer: '#25252C',
                        colorBorder: 'transparent',
                        hoverBorderColor: 'transparent',
                        activeBg: '#25252C',
                        hoverBg: '#25252C',
                        inputFontSize: 14,
                        paddingBlock: 12,
                        paddingInline: 16,
                        colorText: 'white',
                        colorTextPlaceholder: 'white',
                        borderRadius: 16,
                        activeBorderColor: 'transparent',
                        activeShadow: 'none'
                    },
                },
            }}
        >
            <div className={className('wrapper')}>
                <Input
                    placeholder="Поиск"
                    value={value}
                    onChange={(e) => handleChange(e.target.value)}
                    prefix={<img src={search} />}
                    onFocus={() => setFocused(true)}
                    onPressEnter={handleSearch}
                    onBlur={() => setFocused(false)}
                    ref={inputRef}
                />
                {(isFocused || isSuggestFocused) && (useSearchButton || suggests.length) ? (
                    <div className={className('suggest')} ref={suggestRef}>
                        <Flex vertical gap={32}>
                            <Flex vertical>
                                {suggests.map(suggest => (
                                    <Link to={`${Routes.SearchResult}?search=${suggest.value}`}>
                                        <Flex align="center" justify="space-between" className={
                                            className({
                                                elem: 'suggest-item', type: suggest.type === SearchSuggestType.Time ? 'time' : 'popular'
                                            })
                                        }>
                                            <Flex align="center" gap={8}>
                                                {<img src={suggest.type === SearchSuggestType.Time ? time : whiteSearch} />}
                                                {suggest.value}
                                            </Flex>
                                            {suggest.delete && <img className={className('suggest-item-close')} onClick={(e) => {
                                                e.preventDefault();
                                                suggest.delete?.();
                                            }} src={close} />}
                                        </Flex>
                                    </Link>
                                ))}
                            </Flex>
                            {useSearchButton && <Button onClick={handleSearch} theme="fade">Перейти к результатам</Button>}
                        </Flex>
                    </div>
                ) : null}
            </div>
        </ConfigProvider>
    );
};