const className = useClassname("search-layout");

export const SearchLayout: FC<{
    search: string,
    onChangeSearch: (v: string) => void,
    onSearch: () => void,
    onUpload: () => void;
}> = ({search, onChangeSearch, onSearch, onUpload}) => {
    
    return (
        <div className={className()}>
            <Flex vertical gap={126}>
                <Flex justify="flex-end">
                    <Button onClick={onUpload}>Загрузить видео</Button>
                </Flex>
                <Flex vertical gap={72} align="center">
                    <div className={className('logo')}><Logo /></div>
                    <div className={className('search')}>
                        <SearchInput value={search} onChange={onChangeSearch} onSearch={onSearch} useSearchButton />
                    </div>
                </Flex>
            </Flex>
        </div>
    );
};
