/* eslint-disable @typescript-eslint/no-empty-function */
import React, { FC, useEffect, useRef, useState } from "react";
import { useClassname } from "../../libs/css";

import "./styles.scss";
import { Flex, Col, Row, Tooltip } from "antd";
import { Button } from "../button";
import { Logo } from "../logo";
import { SearchInput } from "../search-input";
import Video from "../video";
import Tag from "../tag";
import filters from './filters.svg';
import all from './all.svg';
import fun from './fun.svg';
import book from './book.svg';
import sad from './sad.svg';
import { Link } from "react-router-dom";
import { Routes } from "../../libs/application-routes";
import useApiClient from "../../libs/api-client";

const className = useClassname("list-layout");

const types = [
    {
        image: all,
        text: 'Все',
    },
    {
        image: fun,
        text: 'Развлекательные',
    },
    {
        image: book,
        text: 'Познавательные',
    },
    {
        image: sad,
        text: 'Сентиментальные',
    }
];

const filterItems = [
    {
        key: 'time',
        text: 'Время загрузки',
        items: [
            {
                text: 'За последний час',
                key: 'last-hour'
            },
            {
                text: 'Сегодня',
                key: 'today',
            }
        ]
    },
    {
        key: 'duration',
        text: 'Длительность',
        items: [
            {
                text: 'Короткие',
                key: 'short'
            },
            {
                text: 'Длинные',
                key: 'long',
            }
        ]
    },
    {
        key: 'sort',
        text: 'Упорядочить',
        items: [
            {
                text: 'Релевантности',
                key: 'default'
            },
            {
                text: 'Времени загрузки',
                key: 'time',
            },
            {
                text: 'Популярности',
                key: 'popular',
            }
        ]
    },
];

const limit = 20;

export const ListLayout: FC<{
    initialValue: string;
    value: string;
    onChange: (val: string) => void;
    onUpload: () => void;
    onSearch: () => void;
}> = ({value, onChange, onUpload, initialValue, onSearch}) => {
    const [selectedType, setSelectedType] = useState(0);
    const [selectedFilter, setSelectedFilter] = useState(0);
    const [videos, setVideos] = useState<{url?: string, highlight?: string}[] | undefined>(undefined);
    const [offset, setOffset] = useState(0);
    const [error, setError] = useState('');

    const ref = useRef<HTMLDivElement>(null);

    const apiClient = useApiClient();

    useEffect(() => {
        const scrollCb = () => {
            if (window.scrollY + window.innerHeight > ((ref.current?.clientHeight || 0) - 50)) {
                setOffset(prev => {
                    if (prev === videos?.length) {
                        return prev;
                    }
                    if (prev + limit > (videos?.length || 0) + 100) {
                        return prev;
                    }
                    return prev + limit;
                });
            }
        };

        window.addEventListener('scroll', scrollCb);
        return () => {
            window.removeEventListener('scroll', scrollCb);
        }
    }, [videos?.length]);

    useEffect(() => {
        setOffset(0);

        apiClient.getList({search: initialValue, limit, offset}).then((v) => {
            if (v.error) {
                setError(v.error);
            } else {
                setVideos(v.result);
            }
        });
    }, [initialValue]);

    useEffect(() => {
        if (offset === 0) {
            return;
        }

        apiClient.getList({search: initialValue, limit, offset}).then((v) => {
            if (v.error) {
                setError(v.error);
            } else {
                setVideos(prev => [...(prev || []), ...v.result]);
            }
        })
    }, [offset, initialValue]);

    const [isFiltersOpened, setFiltersOpened] = useState(false);

    const hasContent = videos?.length || 0 > 0;

    return (
        <div className={className()} ref={ref}>
            <Flex vertical gap={60}>
                <Flex gap={8} align="center">
                    <Link to={Routes.Main} className={className('logo')}><Logo /></Link>
                    <div className={className('search')}>
                        <SearchInput value={value} onChange={onChange} onSearch={onSearch} />
                    </div>
                    <Button onClick={onUpload}>Загрузить видео</Button>
                </Flex>
                <div className={className('page')}>
                    <div className={className('page-title')}>
                        {!error && videos?.length !== 0 && `Результаты поиска "${initialValue}"`}
                        {error}
                        {videos?.length === 0 && `По запросу "${initialValue}" ничего не найдено`}
                    </div>
                    <Flex
                        justify="space-between"
                        align="center"
                        className={className({elem: 'filters-row', opened: isFiltersOpened})}
                        gap={10}
                    >
                        <div>
                            {!isFiltersOpened && hasContent && (
                                <Flex gap={8}>
                                    {types.map((type, i) => (
                                        <Tooltip title="Фильтр пока не работает" key={i} trigger="hover">
                                            <div>
                                                <Tag
                                                    isSelected={i === selectedType}
                                                    onClick={() => setSelectedType(i)}
                                                >
                                                    <img src={type.image} /> {type.text}
                                                </Tag>
                                            </div>
                                        </Tooltip>
                                    ))}
                                </Flex>
                            )}
                            {isFiltersOpened && hasContent && (
                                <Flex gap={8}>
                                    {filterItems.map((type, i) => (
                                        <Tooltip title="Фильтр пока не работает" key={i} trigger="hover">
                                            <div>
                                                <Tag
                                                    isSelected={i === selectedFilter}
                                                    onClick={() => setSelectedFilter(i)}
                                                    key={i}
                                                >
                                                    {type.text}
                                                </Tag>
                                            </div>
                                        </Tooltip>
                                    ))}
                                </Flex>
                            )}
                        </div>
                        
                        {hasContent && <Button theme="inverse" onClick={() => setFiltersOpened(prev => !prev)}><img src={filters} /> Фильтры</Button>}
                        {isFiltersOpened && hasContent && (<div className={className('filters-row-content')}>
                            <Flex vertical gap={8}>
                                {filterItems.find((type, i) => i === selectedFilter)?.items.map(item => (
                                    <Tooltip title="Фильтр пока не работает" key={item.key} trigger="hover">
                                        <div className={className('filters-row-content-item')}>{item.text}</div>
                                    </Tooltip>
                                ))}
                            </Flex>
                        </div>)}

                        
                    
                    </Flex>
                    <Row gutter={[16, 16]} className={className('items')}>
                        {videos?.map((video, i) => (
                            <Col xs={{span: 24}} md={{span: 12}} lg={{span: 8}} xl={{span: 6}} key={`${video.url}-${i}`}>
                                <div className={className('item')}>
                                    {video.url && <Video href={video.url} highlight={video.highlight} />}
                                </div>
                            </Col>
                        ))}
                    </Row>
                </div>
            </Flex>
        </div>
    );
};
