import axios, { AxiosResponse } from "axios";

interface UploadParams {
    url: string;
    quality: number;
}

interface ListParams {
    search: string;
    limit?: number;
    offset?: number;
}

interface SuggestParams {
    search: string;
}

interface ListApiResponse {
    error?: string;
    hits?: {
        hits?: {
            _source?: {
                url?: string;
                audio?: {
                    time?: number;
                    text?: string;
                    text_en?: string;
                    text_ru?: string;
                },
                video?: {
                    time?: number;
                    text?: string;
                    text_en?: string;
                    text_ru?: string;
                }
            },
            highlight?: {
                'audio.text': string[];
            }
        }[]
    }
}

interface ApiClient {
    getList: (params: ListParams) => Promise<{
        result: {
            url?: string;
            highlight?: string;
        }[];
        error?: string;
    }>;
    upload: (params: UploadParams) => Promise<{
        url?: string;
        audioText?: string;
        audioTime?: number;
        videoText?: string;
        videoTime?: number;
        error?: string
    }>;
    getSuggests: (params: SuggestParams) => Promise<string[]>;
}

const apiBasePath = "http://91.224.87.158:8000";

const useApiClient = (): ApiClient => {
    return {
        getList: async ({search, limit = 10, offset}) => axios
            .get(`${apiBasePath}/api?search=${search.trim()}&limit=${limit}&offset=${offset}`)
            .then(function (response: AxiosResponse<ListApiResponse>) {
                return {
                    result: response.data.hits?.hits?.map((item) => {
                        return {
                            url: item._source?.url,
                            highlight: item.highlight?.["audio.text"]?.[0],
                        };
                    }) || ([] as { url?: string }[])
                };
            })
            .catch(function (e) {
                console.log('e', e);
                return {
                    result: [],
                    error: 'Произошла ошибка'
                }
            }),
        upload: async ({url, quality}) => axios
            .get(`${apiBasePath}/api?url=${url.trim()}&quality=${quality}`)
            .then(function (response: AxiosResponse<ListApiResponse>) {
                const source = response.data.hits?.hits?.[0]?._source;

                return {
                    error: response.data.error,
                    url: source?.url,
                    audioText: source?.audio?.text_ru || source?.audio?.text || source?.audio?.text_en,
                    audioTime: source?.audio?.time,
                    videoText: source?.video?.text_ru || source?.video?.text || source?.video?.text_en,
                    videoTime: source?.video?.time,
                }
            })
            .catch(function () {
                return {error: 'Произошла ошибка'}
            }),
        getSuggests: async ({search}) => axios
            .get(`${apiBasePath}/api?suggest=${search.trim()}`)
            .then(function (response: AxiosResponse<ListApiResponse>) {
                return [] as string[];
            })
            .catch(function () {
                return [] as string[];
            }),
    };
};

export default useApiClient;
