import React, { FC, PropsWithChildren } from "react";
import { useClassname } from "../../libs/css";

import "./styles.scss";
import { Flex } from "antd";

const Tag: FC<PropsWithChildren<{
    isSelected?: boolean;
    onClick?: () => void;
}>> = ({isSelected, children, onClick}) => {
    const className = useClassname("tag");

    return (
        <Flex
            align="center" 
            gap={8}
            onClick={onClick}
            className={className({selected: isSelected, selectable: !!onClick})}
        >
            {children}
        </Flex>
    );
};

export default Tag;
